import React from 'react'
import { useDimension } from 'Page/StockEntry/GeneralServices/'
import { Link } from 'react-router-dom'

const { height, emptyItem, width } = useDimension(90)

const Settings = () => {
    return (
        <div className="bg-white border radius-all" style={{ minHeight: height, height: height, maxHeight: height }} >
            <div className='h4 m-0 p-3 text'>System Settings</div>
            <Link tabIndex='0' className='sku-item' to='/settings/active-stock'>
                <div className='border-top border-bottom p-3 d-flex justify-content-between'>
                    <div>
                        <span className=' LatoBold text'>Active Stock</span>
                        <span className=' LatoBold font-green'>{` (Last Sync:${'19:00 - 30-03-2021)'}`}</span>
                    </div>
                    <div className='iconU-arrowRight text' />
                </div>
            </Link>

        </div >
    )
}

export default Settings